import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useEffect, useState } from 'react';

export const MonthView = ({ firstDay, endDay, onCellClick }) => {
  const [weeksRange, setWeeksRange] = useState(
    Array.from(moment.range(firstDay, endDay).by('week')),
  );
  const [currentMonth, setCurrentMonth] = useState(firstDay.format('MM'));

  useEffect(() => {}, []);

  return (
    <div className="table-responsive">
      <h5>{firstDay.format('MMMM')}</h5>
      <table className="table table-hover table-bordered table-sm">
        <thead>
          <tr>
            {moment.weekdays().map(weekDay => (
              <th key={weekDay} className="days-of-week">
                {weekDay}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {weeksRange.map(week => {
            var daysRange = Array.from(
              moment.range(week.startOf('week').toDate(), week.endOf('week').toDate()).by('day'),
            );

            return (
              <tr>
                {daysRange.map(d => (
                  <td
                    className={d.format('MM') != currentMonth ? 'text-secondary' : 'text-body'}
                    onClick={e => console.log(e)}
                  >
                    {d.format('DD')}
                  </td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};
